footer {
	width: 100%;
	border-top: 1px solid #ccc;
	padding: 50px 0px;
	.inner {
		width: 1180px;
		margin: 0 auto;
		text-align: center;
		p {
			font: 12px/1 "arial";
			color: #888;
			text-align: center;
		}
	}
}

@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 100%;
			padding: 0px 3vw;
		}
	}
}
